header {
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*======================CTA================*/
.cta{
    margin-top: 2rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*======================HEADER SOCIALS================*/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}


/* for the line after icon in vertical direction */
.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}


/*======================IMAGE================*/

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
    padding: 0rem 0rem 0rem 0rem;
    
}

/*======================Scroll Down================*/
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* Media Query for medium devices :- tablets*/
@media screen and (max-width: 1024px){
    header{
        /* 68 */
        height: 100vh;
    }
    
}

/* Media Query for small devices :- Phone*/
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    
    .header__socials,
    .scroll__down{
        display: none;
    }
}