nav{
    background: rgba(0,0,0,0.4);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: white;
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.9);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}

/* we want to be able to change the active class and put it on what we click :- for this we will use useState (react hooks)*/